.gamebox {
    /* flex: 40%; */
    margin-bottom: 5%;
    padding: 1%;
    text-align: center;
    border: 2px solid rgb(37, 37, 37);
    box-shadow: -1.5px 1.5px 0px 0px rgb(80, 80, 80);
    width: 40%;
    /* height: 100%; */
    border-radius: 15px;
    background-color: rgb(70, 70, 70);
    overflow: hidden;
}

.gameboxTitleBackgroundColor {
    background-color: rgb(0, 0, 0);
    width: 150%;
    /* height: 7%; */
    /* top: 0; */
    margin-left: -25%;
    margin-top: -5%;
    padding-top: 3%;
    padding-bottom: 1%;
    /* margin-bottom: 0; */
}

.gameboxTitle {
    background-color: rgb(0, 0, 0);
    width: 100%;
    font-size: large;
}

.images {
    margin-top: 5%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
}

.imageLink {
    margin-bottom: 5%;
    width: 40%;
    height: 40%;
}

.subimage {
    width: 100%;
    height: 100%;
    border-radius: 15px;
}

/* .downloadBtn {
    bottom: 0;
} */

.innerGamebox {
    height: 95%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}